<template>
<div class="homepage">
    <el-container>
        <el-header style="height:80px;border-bottom:1px solid #000">
            <img src="../assets/logo.png">
            <el-menu class="el-menu-demo" mode="horizontal">
                <el-menu-item index="1"><router-link to="/" @click.native="go">返回主页</router-link></el-menu-item>
                <el-menu-item index="2">provisional</el-menu-item>
                <el-menu-item index="3">provisional</el-menu-item>
                <el-menu-item index="4">provisional</el-menu-item>
                <el-menu-item index="5" @click="readAboutMe = true">关于我们</el-menu-item>
            </el-menu>
            <span class="userHead"><i class="usericon userfenzu"></i><p v-if="!loginStatus"><a @click.prevent="showlogin">未登录</a></p>
            <!-- <p v-if="loginStatus">{{loginUser}}</p> -->
            <el-dropdown  v-if="loginStatus" trigger="click" class="queryuserMes">
                <span class="el-dropdown-link">
                    {{loginUser}}<i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item :disabled="type == 1"><router-link to="/userMessage" style="text-decoration:none;">个人信息</router-link></el-dropdown-item>
                    <el-dropdown-item><a @click.prevent="passwordChange = true" href="" style="text-decoration:none;">修改密码</a></el-dropdown-item>
                    <el-dropdown-item><a @click.prevent="zhuxiao" href="" style="text-decoration:none;">注销账号</a></el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
            </span>
            <span class="loginout"><a @click.prevent="loginout" href=""><i class="usericon userfenzu1"></i></a></span>
        </el-header>
        <el-main>
            <div id="welcome" v-if="!loginStatus">
            <!-- <div id="welcome" v-if="false"> -->
                <span class="hello">Hello</span><br>
                <span class="msg">{{msg}}</span>
                <div id="login">
                    <el-button type="primary" @click="showlogin">Login</el-button>
                    <el-button type="primary" @click="showRegister">Register</el-button>
                </div>
            </div>
            <!-- 登录后显示的router-view -->
            <router-view v-if="loginStatus"></router-view>
        </el-main>
    </el-container>
    <el-dialog title="登录" :visible.sync="loginmodel" width="30%">
        <el-form status-icon ref="userlogin" :rules="rule" :model="userlogin">
            <!-- 用户类型 -->
            <el-form-item>
                <el-radio v-model="userlogin.userType" label="0"  size="medium" style="margin-right:27px;">普通用户</el-radio>
                <el-radio v-model="userlogin.userType" label="1"  size="medium" >管理员</el-radio>
                <el-radio v-model="userlogin.userType" label="2"  size="medium" >窗口管理人员</el-radio>
            </el-form-item>
            <!-- 用户名输入 -->
            <el-form-item prop="userNo">
                <el-input prefix-icon="el-icon-edit" v-model="userlogin.userNo" placeholder="请输入用户名" size="small"></el-input>
            </el-form-item>
            <!-- 密码输入与登陆按钮 -->
            <el-form-item prop="password">
                <el-input prefix-icon="el-icon-view" v-model="userlogin.password" placeholder="请输入密码" size="small" show-password></el-input>
            </el-form-item>
            <el-form-item class="item_button">
                <el-button type="primary" size="small" @click="login" :loading="loading">登录</el-button>
                <el-button type="success" size="small" @click="showRegister"  :disabled="userlogin.userType==1">注册</el-button>
                <!-- <el-button type="warning" size="small" @click="">注销</el-button> -->
            </el-form-item>
        </el-form>
    </el-dialog>
    <el-dialog title="注册" :visible.sync="registermodel" width="30%" :close-on-click-modal="false" :before-close="handleClose">
        <el-form :model="userRes" ref="userRes" :rules="rule" status-icon>
          <el-form-item prop="userNo">
            <el-input type="text" v-model="userRes.userNo" size="small" placeholder="用户名"></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input type="text" v-model="userRes.password" size="small" placeholder="密码"></el-input>
          </el-form-item>
          <el-form-item prop="email">
            <el-input type="text" v-model="userRes.email" size="small" placeholder="电子邮箱"></el-input>
          </el-form-item>
          <el-form-item prop="phone">
            <el-input type="text" v-model.number="userRes.phone" size="small" placeholder="电话号码"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="register" type="primary" :loading="zhuce">注册</el-button>
          <el-button @click="reset" type="warning">重置</el-button>
        </span>
    </el-dialog>
    <el-dialog title="关于我们" :visible.sync="readAboutMe">
        <div class="demo-image__lazy" style="max-height:550px;overflow:auto;">
            <el-image v-for="(url) in urls" :key="url" :src="url" lazy></el-image>
        </div>
    </el-dialog>
    <el-dialog title="密码修改" :visible.sync="passwordChange" width="30%">
        <el-form status-icon ref="changePass" :rules="rule" :model="changePass">
            <el-form-item prop="old">
                <el-input v-model="changePass.old" placeholder="请输入旧密码" size="small"></el-input>
            </el-form-item>
            <el-form-item prop="new">
                <el-input v-model="changePass.new" placeholder="请输入新密码" size="small"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="warning" size="small" @click="modifyPass" :loading="xiugai">修改</el-button>
                <el-button type="success" size="small" @click="passreset">重置</el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
</div>
</template>
<script>


export default {
    data(){
        return{
            msg: '政务办理电子预约排队系统',
            loginmodel: false,
            registermodel: false,
            loading: false,
            loginStatus: false,  //表示登录状态
            loginUser: "",  //登录用户
            readAboutMe: false,
            passwordChange: false,
            activename: '1',
            zhuce: false,
            xiugai: false,
            type : '',
            userlogin:{
                userType: '0',
                userNo:'',
                password:''
            },
            userRes:{
                userType: '0',
                userNo:"",
                password:"",
                email:"",
                phone:""
            },
            changePass:{
                old:"",
                new:""
            },
            rule:{
                userNo:[{required:true,trigger:"blur",message:'用户名不能为空'}],
                password:[{required:true,trigger:"blur",message:"密码不能为空"}],
                email:[{type:"email",message:"请输入正确的邮箱地址",trigger:["blur"]}],
                phone:[{type:"number",message:"电话格式有误",trigger:"blur"}],
                new:[{required:true,trigger:"blur",message:"密码不能为空"}],
                old:[{required:true,trigger:"blur",message:"密码不能为空"}]
            },
            urls:[require('../assets/Aboutus.png')]
        }
    },
    mounted(){
        this.refresh()
    },
    methods:{
        go(){
            history.go(0)
        },
        refresh(){
            this.loginUser = this.getCookie("userNo")
            if(this.loginUser == "")
                this.loginStatus = false
            else{
                this.loginStatus = true
                let type = sessionStorage.getItem('userType')
                if(type == '0') //普通用户
                {
                    this.type = '0'
                    this.$router.push('/common')
                }
                     //管理员
                if(type == '1')
                {
                    this.type = '1'
                    this.$router.push('/admin')
                }
                if(type == '2'){
                    this.type = '2'
                    this.$router.push("/windowManage")
                }
            }
        },
        login(){
            this.$refs["userlogin"].validate((valid)=>{
                if(valid){//发送
                this.loading = true
                try{
                    this.axios.Login(this.userlogin).then((res)=>{
                        if(res.success)
                        {
                            this.MessageBox(res.message,'success') 
                            this.setcookie("userNo",this.userlogin.userNo,1)  //cookie一小时过期
                            sessionStorage.setItem('userNo',this.userlogin.userNo)
                            sessionStorage.setItem('id',res.dataList[0].id)
                            sessionStorage.setItem('userType',res.dataList[0].userType)
                            this.$store.commit('setNowUserNo',this.userlogin.userNo)
                            setTimeout(function(){this.loading = false;history.go(0)},1000)
                        }else{
                            this.MessageBox(res.message,'error')
                            this.loading = false
                        }
                    })
                }
                catch(error){return}
                }else{
                this.$message.error("表单有误")
                }
            })
        },
        loginout(){
            if(this.loginUser != "")
            {
                this.clearcookie("userNo")
                this.$router.push('/')
                history.go(0)
            }
        },
        showlogin(){
            this.loginmodel = true
        },
        showRegister(){
            this.registermodel = true
        },
        MessageBox(mes,types){
            this.$notify({
                message: mes,
                type: types
            });
        },
        register(){
            this.zhuce = true
            this.$refs["userRes"].validate((valid)=>{
                if(valid){//发送
                    this.axios.Register(this.userRes).then((res)=>{
                        if(res.success)
                        {
                            this.MessageBox(res.message,'success')
                            setTimeout(function(){this.zhuce=false;history.go(0)},2000)
                        }else{
                            this.MessageBox(res.message,'error')
                            this.zhuce = false
                        }
                    })
                }else{
                    this.$message.error("表单有误")
                    this.zhuce = false
                }   
            })
        },
        reset(){
            this.$refs['userRes'].resetFields();
        },
        passreset(){
            this.$refs['changePass'].resetFields();
        },
        handleClose(done){
            this.$confirm('确认关闭？表单信息可能无法保存！').then(_ => {
                this.reset()
                done()
            })
        },
        setcookie(cname,cvalue,exhours){
            var d = new Date()
            d.setTime(d.getTime()+(exhours)*60*60*1000)
            //设置cookie过期时间
            var expires = 'expires='+d.toUTCString()
            document.cookie = cname+"="+cvalue+";"+expires+";path=/"
        },
        getCookie(cname){
            var name = cname+"="
            var ca = document.cookie.split(";")
            for(var i=0;i<ca.length;i++)
            {
                var c = ca[i]
                while(c.charAt(0)==' ')
                    c= c.substring(1)
                if(c.indexOf(name) != -1)
                    return c.substring(name.length,c.length)
            }
            return ""
        },
        //删除cookie
        clearcookie(name){
            this.setcookie(name,"",-1)
        },
        modifyPass(){
            this.xiugai =true
            let data ={
                'id':sessionStorage.getItem('id'),
                'userNo':sessionStorage.getItem('userNo'),
                'newPassword':this.changePass.new,
                'password':this.changePass.old
            }
            this.axios.modifyUser(data).then((res)=>{
                if(res.success){
                    this.$message.success('密码修改成功,请重新登录')
                    this.clearcookie('userNo')
                    setTimeout(function(){this.xiugai=false;history.go(0)},1000)
                }else{
                    this.$message.error(res.message)
                }
            })
        },
        //删除账号
        zhuxiao(){
            this.$confirm('此操作将永久删除本账号所有信息,是否继续?','警告',{
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type:'warning'
            }).then(()=>{
                this.axios.removeUser({'id':sessionStorage.getItem('id')}).then((res)=>{
                    if(res.success){
                        this.MessageBox('注销成功,此账号将不能再登录','warning')
                        this.clearcookie('userNo')
                        setTimeout(function(){history.go(0)},1000)
                    }else{
                        this.MessageBox('注销失败','error')
                    }
                })
            }).catch(()=>{})
        }
    }
}
</script>
<style lang="scss">
body{
    overflow: hidden;
}
.homepage{
    height: 100%;
    width:100%;
}
.el-container {
    a{
        text-decoration: none;
    }
    height: 100%;
    width: 100%;
    .el-header,.el-footer {
        position: relative;
        right: 10px;
        border-radius: 0px 0px 10px 15px;
        box-shadow:0px 0px 2px #000;
        text-align: left;
        p{
            position: relative;
            left: 30px;
            bottom: 37px;
        }
        .queryuserMes{
            position: relative;
            left: 10px;
        }
    }
    .el-main{
        #welcome{
            .hello{
                font-size: 200px;
            }
            .msg{
                position: relative;
                top: 40px;
                font-size: 30px;
            }
            #login{
                position: relative;
                top: 100px;
                left: 10px;
                .el-button{
                    height: 50px;
                    width: 20%;
                    margin-right: 10px;
                }
            }
            .email{
                position: relative;
                top:150px;
                font-size: 20px;
                a{
                    text-decoration: underline; 
                    color: #409EFF; 
                }
                a:hover{
                    color:red;
                }
            }
        }
    }
    img{
        position: relative;
        left: 100px;
    }
    .el-menu{
        width: 600px;
        position: absolute;
        left: 490px;
        top:16px;
        .el-menu-item{
            margin-left: 5px;
            margin-right: 5px;
        }
    }
    .loginout{
        position: absolute;
        right: 50px;
        top: 37px;
    }
    .userHead{
        position: absolute;
        right: 180px;
        top: 37.5px;
    }
}
.el-dialog{
    .el-button{
        margin-left: 5px;
        margin-right: 5px;
        width:30%;
    }
    position: relative;
    left: 10px;
    .el-dialog__body{
        text-align: center;
        padding: 0px;
        padding-top: 5px;
        .el-form-item{
           padding-bottom: 10px;
           margin-bottom: 0px;
        }
        .el-input{
            width: 60%;
            padding-top: 5px;
        }
        .el-form-item__error{
            position: relative;
            text-align: left;
            left: 90px;
        }
    }
    .el-dialog__header{
        background-color: #313E4B;
        .el-dialog__title{
            color: #E0FFFF
        }
    }
    .el-dialog__footer{
        text-align: center;
    }
}
</style>




